<template>
  <nav>
    
    <v-app-bar fixed
      density="compact"
      color="grey-lighten-3"
      flat
    >
      <v-app-bar-nav-icon class="text-grey" @click="drawer = !drawer"></v-app-bar-nav-icon>
      
      <v-app-bar-title class="text-uppercase text-grey">
        <span class="font-weight-light">Todo</span>
        <span>Ninja</span>
      </v-app-bar-title>

      <v-spacer></v-spacer>

      <!-- Dropdown menu -->
      <v-menu>
        <template v-slot:activator="{ props }">
          <v-btn
            color="grey"
            v-bind="props"
          >
            <v-icon start icon="mdi-menu-down"></v-icon>
            <span>menu</span>
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            v-for="link in links"
            :key="link"
            router :to="link.route"
          >
            <v-list-item-title>{{ link.text }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>

      <v-btn>
        <span>Sign Out</span>
        <v-icon end icon="mdi-exit-to-app" class="ml-4"></v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer
      v-model="drawer"
      class="bg-primary"
      temporary
      location="left"
      width="240"
    >
      <v-row class="d-flex justify-center mt-5 mb-1">
        <v-avatar size=100>
          <v-img src="/assets/member_01.png"></v-img>
        </v-avatar>
      </v-row>
      <v-row class="d-flex justify-center mt-1 mb-2">
        <p>
          The Net Ninja
        </p>
      </v-row>
      <v-row class="d-flex justify-center my-2">
        <PopUp />
      </v-row>
      <v-list>
        <v-list-item
          v-for="(link, index) in links"
          :key="index"
          :value="link"
          active-color="red"
          router :to="link.route"
        >
          <template v-slot:prepend>
            <v-icon start :icon="link.icon" class="text-white"></v-icon>
          </template>
          <v-list-item-title class="text-white">{{ link.text }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>

<script>
import PopUp from './PopUp'
export default {
  components: {
    PopUp
  },
  data() {
    return {
      drawer: false,
      links: [
        { text: 'Dashboard', icon: 'mdi-view-dashboard', route: '/'},
        { text: 'My Projects', icon: 'mdi-folder', route: '/projects'},
        { text: 'Team', icon: 'mdi-account-group', route: '/team'},
      ]
    }
  }
}
</script>

