<template>
  <v-dialog max-width="600px">
    <template v-slot:activator="{ props }">
      <v-btn
        color="secondary"
        v-bind="props"
        variant="flat"
      >
        Add new project
      </v-btn>
    </template>

    <v-card>
      <v-card-title>
        <h2>Add a new project</h2>
      </v-card-title>

      <v-card-text>
        <v-form class="px-3" ref="form">
          <v-text-field v-model="title" label="Title" color="primary" bg-color="white" prepend-icon="mdi-folder" :rules="inputRules"></v-text-field>
          <v-textarea label="Information" v-model="content" prepend-icon="mdi-pencil" bg-color="white" :rules="inputRules"></v-textarea>
          
          <v-menu>
            <template v-slot:activator="{ props }">
              <v-text-field :rules="inputRules" value="formattedDate" v-bind="props" label="Due date" prepend-icon="mdi-calendar-range" bg-color="white">
              <v-date-picker model="due"></v-date-picker>
              </v-text-field>
            </template>
            <v-list>
              <v-list-item>
              </v-list-item>
            </v-list>
          </v-menu>

          <v-spacer></v-spacer>
          
          <v-btn variant="flat" class="bg-secondary mx-0 mt-3" @click="submit">Add Project</v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import format from 'date-fns/format'
export default {
  data () {
      return {
        title: '',
        content: '',
        due: null,
        inputRules: [
          v => v.length >=3 || 'Minimum length is 3 characters'
        ]
      }
  },
  methods: {
    submit() {
      if(this.$refs.form.validate()) {
        console.log(this.title, this.content)
      }
    }
  },
  computed: {
    formattedDate() {
      return this.due ? format(this.due, 'Do MMM YYYY') : ''
    }
  }
}
</script>
  
